* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  font-family: Montserrat;
  padding: 0;
  margin: 0;
}
.whole {
  margin-top: 60px;
}

.header-search-nav {
  background-color: rgb(63, 51, 63);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  height: 50px;
  width: 100%;
  color: white;
  -webkit-box-shadow: 0 4px 4px #cccccc;
  box-shadow: 0 4px 4px #cccccc;
}

.search-nav-img {
  padding-left: 10px;
}
.header-nav-links {
  height: 100%;
}

.header-searchbar {
  padding: 0 40px;
}
.header-searchbar input {
  padding: 5px 20px;
  border-radius: 15px;
  border: 2px solid white;
  outline: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.navlist-links {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: end;
  -ms-flex-pack: end;
  justify-content: flex-end;
  height: 30px;
  width: 100%;
  list-style-type: none;
  margin: 0;
  margin-top: 5px;
  padding-right: 10px;
}
.navlist-link {
  font-family: Montserrat;
  padding: 5px 10px;
  margin: 5px 5px;
  width: auto;
  border-radius: 4px;
  color: rgb(63, 51, 63);
}

.navlist-link:hover {
  -webkit-animation: move2 300ms forwards cubic-bezier(0.075, 0.82, 0.165, 1);
  animation: move2 300ms forwards cubic-bezier(0.075, 0.82, 0.165, 1);
  cursor: pointer;
}

/* Main*/
.main {
  background: white;
  height: auto;
  width: 100%;
}

.display-img {
  background-color: rgb(63, 51, 63);
  margin: auto;
  width: 100%;
  height: 300px;
  margin-top: 40px;
  -webkit-box-shadow: 3px 5px 10px 0.5px #7e7d7d;
  box-shadow: 3px 5px 10px 0.5px #7e7d7d;
}

.display-img img {
  height: 100%;
  width: 100%;
}

@media (min-width: 550px) {
  .display-img {
    width: 70%;
    height: 300px;
  }
}

@media (min-width: 750px) {
  .display-img {
    width: 80%;
    height: 420px;
  }
}

@media (min-width: 950px) {
  .display-img {
    width: 80%;
    height: 550px;
  }
}

.main-sections {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  width: 80%;
  margin: auto;
  margin-top: 50px;
  margin-bottom: 50px;
  height: 1050px;
}

.main-section {
  background: -webkit-gradient(
      linear,
      left top,
      left bottom,
      color-stop(10%, rgb(230, 208, 230)),
      to(transparent)
    ),
    white;
  background: -o-linear-gradient(top, rgb(230, 208, 230) 10%, transparent), white;
  background: linear-gradient(to bottom, rgb(230, 208, 230) 10%, transparent), white;
  width: 80%;
  height: 70%;
  -webkit-box-shadow: 3px 5px 10px 0.5px #7e7d7d;
  box-shadow: 3px 5px 10px 0.5px #7e7d7d;
  margin: 10px 10px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
}
.main-section h1 {
  padding: 10px 20px;
  color: white;
  font-weight: 900;
  font-size: 50px;
}

.main-section p {
  padding: 20px 20px;
  font-size: 18px;
  font-weight: 800;
  color: rgb(63, 51, 63);
}

.main-section button {
  padding: 18px 20px;
  background-color: rgb(230, 208, 230);
  border: none;
  width: 50%;
  margin: 10px 20px;
  border-radius: 2px;
  color: white;
  font-size: 18px;
  -webkit-box-shadow: 3px 2px 9px 0.5px #bebcbc;
  box-shadow: 3px 2px 9px 0.5px #bebcbc;
}

#center h1 {
  color: white;
  font-size: 50px;
}
#center p {
  color: rgb(230, 208, 230);
  font-size: 18px;
}
#center button {
  -webkit-box-shadow: 3px 2px 9px 0.5px #252525;
  box-shadow: 3px 2px 9px 0.5px #252525;
  background-color: rgb(63, 51, 63);
}

button:hover {
  -webkit-animation: move 300ms forwards cubic-bezier(0.075, 0.82, 0.165, 1);
  animation: move 300ms forwards cubic-bezier(0.075, 0.82, 0.165, 1);
}

#center {
  background: rgb(63, 51, 63);
  width: 90%;
  height: 75%;
}

#end h1 {
  color: rgb(63, 51, 63);
  font-size: 35px;
}
#end p {
  color: rgb(194, 153, 194);
}

@media (min-width: 800px) {
  .main-sections {
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-direction: row;
    flex-direction: row;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    height: 650px;
  }

  .main-section {
    width: 40%;
    min-width: 250px;
    height: 70%;
    margin: 0 10px;
  }

  #center {
    width: 50%;
    min-width: 270px;
    height: 75%;
  }
}

