    width: 220px;
    height: 300px;
  }
}

@media (min-width: 900px) {
  .container {
    width: 320px;
    height: 400px;
  }
}

@media (min-width: 1200px) {
  .container {
    width: 420px;
    height: 500px;
  }
}
/*Footer*/

.footer-list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  color: white;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  height: 200px;
  width: 100%;
  background: rgb(63, 51, 63);
  list-style: none;
  -webkit-box-shadow: 0px -2px 10px 0.5px #757575;
  box-shadow: 0px -2px 10px 0.5px #757575;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  border-left: 8px solid #757575;
}

.footer-links {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -ms-flex-direction: row;
  flex-direction: row;
  list-style: none;
  -webkit-box-shadow: 0px -2px 10px 0.5px rgb(36, 32, 32);
  box-shadow: 0px -2px 10px 0.5px rgb(36, 32, 32);
}

.footer-link {
  padding: 6px 6px;
  margin: 5px 5px;
}

@-webkit-keyframes move {
  0% {
    -webkit-transform: scale(1);
    transform: scale(1);
    -webkit-filter: brightness(1);
    filter: brightness(1);
  }
  100% {
    -webkit-transform: scale(1.2);
    transform: scale(1.2);
    -webkit-filter: brightness(1.2);
    filter: brightness(1.2);
    color: violet;
  }
}

@keyframes move {
  0% {
    -webkit-transform: scale(1);
    transform: scale(1);
    -webkit-filter: brightness(1);
    filter: brightness(1);
  }
  100% {
    -webkit-transform: scale(1.2);
    transform: scale(1.2);
    -webkit-filter: brightness(1.2);
    filter: brightness(1.2);
    color: violet;
  }
}

@-webkit-keyframes move2 {
  0% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
  100% {
    -webkit-transform: scale(1.2);
    transform: scale(1.2);
    font-weight: 800;
  }
}

@keyframes move2 {
  0% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
  100% {
    -webkit-transform: scale(1.2);
    transform: scale(1.2);
    font-weight: 800;
  }
}
